import React from 'react'
import { NavLink } from 'react-router-dom'

function Navbar() {
    return (
        <>
            <header className="container">
            <nav className="navbar  navbar-expand-lg navbar-light bg">
                <div className="container-fluid">
                        <NavLink to="/" className="navbar-brand" >AGTech</NavLink>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                            <li className="nav-item">
                                <NavLink exact to="/" className="nav-link" activeClassName="active_link" >Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact to="/about" className="nav-link" activeClassName="active_link" >About</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact to="/services" className="nav-link" activeClassName="active_link" >Services</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink exact to="/contact" className="nav-link" activeClassName="active_link" >Contact</NavLink>
                            </li>

                        </ul>

                    </div>
                </div>
            </nav>
            </header>
        </>
    )
}

export default Navbar
